.search-results {
    min-height: 60vh;
    margin-bottom: 50px;
}

.search-results__movies {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 80px;
    margin-left: -20px;
    margin-right: -20px;
    padding: 20px;
}
.search-results__movie {
    flex-basis: 25%;
    width: 25%;
    padding: 20px;
    cursor: pointer;
    position: relative;
}
.search-results__movie-image-wrapper {
    position: relative;
    width: auto;
    border-radius:10px;
    overflow: hidden;
}

.search-results__movie-image {
    display: block;
}

.search-results__movie-description {
    padding-top: 20px;
}

.search-results__movie-title {
    font-size: 18px;
    font-weight: bold;
    color: #e2e2e2;
}
.search-results__movie-rating {
    font-size: 16px;
    color: #e2e2e2;
    padding-top: 5px;
}
.search-results__movie-release-date,
.search-results__movie-genres,
.search-results__movie-genre {
    font-size: 16px;
    color: #8798aa;
    padding-top: 5px;
}
.search-results__movie-genre:after {
    content: ', '
}
.search-results__movie-genres > span:last-child:after {
    content: ''
}
.search-results__movie-add:before {
    content: '+';
}
.search-results__movie-add {
    display: none;
    position: absolute;
    bottom: 140px;
    right: 30px;
    width: 38px;
    height: 38px;
    min-width: 38px;
    min-height: 38px;
    padding: 10px;
    margin: 2px;
    border-radius: 30px;
    font-size: 20px;
    text-align:center;
    line-height: 20px;
    color: #fff;
    background-color:#14c3f4;
}
.search-results__movie-add:active:before {
    content: 'Added âœ“';
}
.search-results__movie-add:active {
    width: 80px;
    font-size: 12px;
}
.search-results__movie:hover .search-results__movie-image-wrapper {
    box-shadow: 0px 0px 0px 3px #14c3f4;
}
.search-results__movie:hover .search-results__movie-add {
    display: block;
}
.search-results__nothing-found {
    padding: 20px;
    text-align: center;
    color: #8798aa;
}

@media screen and (max-width: 769px) {
    .search-results__movie {
        flex-basis: 33.33%;
        width: 33.33%;
    }
}

@media screen and (max-width: 480px) {
    .search-results__movies {
        display: block;
        margin-top: 0;
        margin-left: 0;
        margin-right: 0;
        padding: 0;
    }
    .search-results__movie {
        display: flex;
        width: 100%;
        justify-content: flex-start;
        align-items: center;
    }
    .search-results__movie-image-wrapper {
        width: 120px;
        min-width: 120px;
    }
    .search-results__movie-description {
        padding: 0 0 0 20px;
    }

    .search-results__movie-title {
        font-size: 16px;
    }
    .search-results__movie-rating, 
    .search-results__movie-release-date,
    .search-results__movie-genres,
    .search-results__movie-genre {
        font-size: 12px;
    }    
    .search-results__movie-add {
        display: block;
        position: relative;
        top:0;
        left:0;
        height: 25px;
        min-height: 25px;
        width: 110px;
        padding: 6px 5px 5px 5px;
        margin-top: 5px;
        font-size: 11px;
        line-height: 11px;
        color: #fff;
        background-color:#14c3f4;
    }
    .search-results__movie-add:after {
        content: ' Add to watched'
    }
    .search-results__movie-add:active:before {
        content: 'âœ“ Added';
    }
    .search-results__movie-add:active:after {
        content: '';
    }
    .search-results__movie-add:active {
        width: 70px;
        font-size: 11px;
    }
}